import React, {useState, useEffect} from 'react';
import * as api from '../../../api/api';
const Leauges = (userinfo) => {
    const [tier_Leaugeinfo, setTier_Leagueinfo] = useState('');
    const [Leagues_leaugeinfo, setLeagues_Leagueinfo] = useState('');
    useEffect(() => {
        api.getleagueFirst(userinfo.id)
        .then(response => setTier_Leagueinfo(response.data))
        .catch(error => console.log(error))
        
        api.getleagueSecond(tier_Leaugeinfo.leagueId)
        .then(response => setLeagues_Leagueinfo(response))
        .catch(error => console.log(error))
    },[userinfo])
    console.log(Leagues_leaugeinfo)
    return (
        <div>

        </div>
    );
}

export default Leauges;